import Vue from 'vue'
import VueRouter from 'vue-router'

// 在 Vue CLI 创建的项目中 @ 表示 src 目录
// 它是 src 目录的路径别名
// 好处：它不受当前文件路径影响
// 注意：@ 就是 src 路径，后面别忘了写那个斜杠
// 建议：如果加载的资源路径就在当前目录下，那就正常写；但凡是需要父级路径查找的都建议使用 @
import Login from '@/views/login/'
import Home from '@/views/home/'
import Layout from '@/views/layout/'
import Article from '@/views/article/'
import Publish from '@/views/publish/'
import Image from '@/views/image/'
import Comment from '@/views/comment/'
import Settings from '@/views/settings/'
import Fans from '@/views/fans/'

Vue.use(VueRouter)

// 路由配置表
const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/',
    // 浏览器会出现一个警告：命令路由 layout 有一个默认子路由，这个名字没有意义。所以警告
    // 正确做法是：如果有默认子路由，就不要给父路由起名字了
    // name: 'layout', // 嵌套路由的父路由不需要起名字
    component: Layout,
    children: [
      {
        path: '', // post 为空，会作为默认子路由渲染
        name: 'home',
        component: Home
      },
      {
        path: '/article',
        name: 'article',
        component: Article
      },
      {
        path: '/publish',
        name: 'publish',
        component: Publish
      },
      {
        path: '/image',
        name: 'image',
        component: Image
      },
      {
        path: '/comment',
        name: 'comment',
        component: Comment
      },
      {
        path: '/settings',
        name: 'settings',
        component: Settings
      },
      {
        path: '/fans',
        name: 'fans',
        component: Fans
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 路由导航守卫(路由拦截器) --> 所有页面的导航都会经过这里
//  to：要去的路由信息
//  from：来自哪的路由信息
