//   //    如果没有登录，则跳转到登录页面
//   //    如果登录了，则允许通过

//   const user = JSON.parse(window.localStorage.getItem('user'))

//   // 校验非登录页面的登录状态
//   if (to.path !== '/login') {
//     // console.log('需要登录')
