<template>
  <div class="comment-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!-- 面包屑路径导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>评论管理</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- /面包屑路径导航 -->
      </div>
      <!--
        1.把数据列表绑定给表格 data
        2.设计表格列
        3.给表格列绑定要渲染的数据字段
       -->
      <el-table
        class="table-list"
        :data="tableData"
        style="width: 100%"
        stripe
      ><!-- :data="articles" -->
        <el-table-column
          prop="title"
          label="标题"
        >
        </el-table-column>
        <el-table-column
          prop="total_comment_count"
          label="总评论数"
        >
        </el-table-column>
        <el-table-column
          prop="fans_comment_count"
          label="粉丝评论数">
        </el-table-column>
        <el-table-column
          prop="comment_status"
          label="评论状态"
        >
          <template slot-scope="scope">
            {{ scope.row.comment_status ? '正常' : '关闭' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="comment_status"
          label="操作"
        >
          <template slot-scope="scope">
            <!-- active-color="#13ce66" inactive-color="#ff4949" 关闭和开启的颜色设置-->
            <el-switch
              v-model="scope.row.comment_status"
              @change="onStatusChange(scope.row)"
              :disabled="scope.row.statusDisabled"
            />
          </template>
        </el-table-column>
      </el-table>
      <!--
        绑定页码
        绑定每页大小
        current-page 控制激活的页码，初始是第一页
        page-sizes 控制可选的每页大小
        page-size 当前显示的每页大小
       -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="page"
        :page-sizes="[10, 20, 30, 40, 100]"
        :page-size.sync="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount"
        background
      />
    </el-card>
  </div>
</template>

<script>
import { getArticles, updateCommentStatus } from '@/api/article'

export default {
  name: 'CommentIndex',
  components: {},
  props: {},
  data () {
    return {
      tableData: [
        {
          title: '我觉得你挺不错',
          total_comment_count: 0,
          fans_comment_count: 0,
          comment_status: true
        },
        {
          title: '需要多一点运气',
          total_comment_count: 0,
          fans_comment_count: 0,
          comment_status: true
        },
        {
          title: '要更加努力',
          total_comment_count: 0,
          fans_comment_count: 0,
          comment_status: true
        },
        {
          title: '永不言弃',
          total_comment_count: 0,
          fans_comment_count: 0,
          comment_status: true
        }
      ],
      articles: [], // 文章数据列表
      totalCount: 0, // 总数据条数
      pageSize: 10,
      page: 1 // 当前激活的页码
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles()
  },
  mounted () {},
  methods: {
    handleSizeChange () {},
    handleCurrentChange (page) {
      // console.log(page)
      // 页码改变，加载指定页码数据
      this.loadArticles(page)
    },
    loadArticles (page = 1) {
      // 让分页组件激活的页面和请求数据的页码保持一致
      this.page = page
      getArticles({
        response_type: 'comment',
        page,
        per_page: this.pageSize
      }).then(res => {
        // console.log(res)
        const { results } = res.data.data
        results.forEach(article => {
          article.statusDisabled = false
        })
        this.articles = results
        this.totalCount = res.data.data.total_count
      })
    },
    onStatusChange (article) {
      // console.log(article)
      // console.log(article.comment_status)
      // 禁用开关
      article.statusDisabled = true
      // 请求提交修改
      updateCommentStatus(article.id.toString(), article.comment_status).then(res => {
        // console.log(res)
        // 启用开关
        article.statusDisabled = false
        this.$message({
          type: 'success',
          message: article.comment_status ? '开启成功' : '关闭成功'
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.table-list {
  margin-bottom: 30px;
}
</style>
