<template>
  <div class="image-list">
    <div class="action-head">
      <el-radio-group v-model="collect" size="small" @change="loadImages(1)">
        <!-- <el-radio-button label="全部" @click.native="loadImages(false)"></el-radio-button> -->
        <!-- <el-radio-button label="收藏" @click.native="loadImages(true)"></el-radio-button> -->
        <el-radio-button :label="false">全部</el-radio-button>
        <el-radio-button :label="true">收藏</el-radio-button>
      </el-radio-group>
      <el-button
        size="small"
        type="success"
        @click="dialogUploadVisible = true"
        v-if="isShowAdd"
      >上传素材</el-button>
    </div>
    <el-row :gutter="10">
      <el-col
        :xs="12"
        :sm="6"
        :md="6"
        :lg="4"
        v-for="(img, index) in images"
        :key="index"
        class="image-item"
        @click.native="selected === index"
      >
        <el-image
          style="height: 100px"
          class="selected"
          fit="cover"
          :src="img.url"
        />
        <div class="selected" v-if="isShowSelected && selected === index"></div>
        <div class="image-action" v-if="isShowAction">
          <el-button
            type="warning"
            :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"
            circle
            size="small"
            @click="onCollect(img)"
            :loading="img.loading"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle size="small"
            @click="onDelete(img)"
            :loading="img.loading"
          ></el-button>
          <!-- <i
            v-loading="true"
            :class="{
              'el-icon-star-on': img.is_collected,
              'el-icon-star-off': !img.is_collected
            }"
            @click="onCollect(img)"
          ></i>
          <i class="el-icon-delete-solid"></i> -->
        </div>
      </el-col>
    </el-row>
    <!-- 分页 -->
    <!-- 切换全部和收藏，分页数据改变以后，页码不会变化，它需要单独处理 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="totalCount"
      :page-size="pageSize"
      :current-page.sync="page"
      @current-change="onPageChange"
    >
    </el-pagination>
    <!-- /分页 -->
    <!-- 弹出的对话框配置 :append-to-body="true" 或者 :modal-append-to-body="false" 浮动到所有页面的上方 -->
    <el-dialog
      title="上传素材"
      :visible.sync="dialogUploadVisible"
      :append-to-body="true"
    >
      <!-- upload 组件本身就支持请求提交上传操作，使用它不用你发请求，它会自己发 -->
      <!-- 请求方法：默认就是 POST -->
      <!-- 请求路径：action ，必须写完整路径 -->
      <!-- 请求头：headers ， -->
      <el-upload
        class="upload-demo"
        drag
        action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
        :headers="uploadHeaders"
        multiple
        name="image"
        :on-success="onUploadSuccess"
        v-if="dialogUploadVisible"
      ><!-- v-if="dialogUploadVisible" 或者 :show-file-list="false" 清除上传日志 -->
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
import { getImages, collectImage, deleteImage } from '@/api/image'

export default {
  name: 'ImageList',
  components: {},
  // 数组声明 prop，不建议使用，不够严谨，而且功能也不够强大，仅适合 demo 演示
  // props: [dddxx],
  // 对象的方式定义 prop，强烈建议使用，更严谨，功能更丰富，
  props: {
    // 是否显示添加素材
    isShowAdd: {
      type: Boolean, // 布尔值
      default: true // 默认值
    },
    // 是否显示收藏和删除操作按钮
    isShowAction: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false, // 默认查询全部素材列表
      images: [], // 图片素材列表
      dialogUploadVisible: false,
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      totalCount: 0, // 总数据条数
      pageSize: 10, // 每页大小
      page: 1, // 当前页码
      selected: null // 选中的索引
    }
  },
  computed: {},
  watch: {},
  created () {
    // 默认页面初始化加载第一页数据
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    loadImages (page = 1) {
      // 重置高亮页码，防止数据和页面不对应
      this.page = page
      getImages({
        collect: this.collect,
        page,
        per_page: this.pageSize
      }).then(res => {
        const results = res.data.data.results
        results.forEach(img => {
          // img 对象本来是没有 loading 数据，这里手动的添加该数据是用来控制每个收藏按钮的 loading 状态
          img.loading = false
        })
        // console.log(res)
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },
    // onCollectChange () {
    //   // console.log(value)
    //   // console.log(this.collect)
    //   this.loadImages(1)
    // },
    onUploadSuccess () {
      // 关闭对话框
      this.dialogUploadVisible = false
      // 更新素材列表
      this.loadImages(this.page)

      this.$message({
        type: 'success',
        message: '上传成功'
      })
    },
    onPageChange (page) {
      // console.log(page)
      this.loadImages(page)
    },
    onCollect (img) {
      // console.log(img)
      // if (img.is_collected) {
      //   // 已收藏，取消收藏
      //   collectImage(img.id, false)
      // } else {
      //   // 没有收藏，添加收藏
      //   collectImage(img.id, true)
      // }

      // 展示 loading
      img.loading = true
      collectImage(img.id, !img.is_collected).then(res => {
        // console.log(res)
        img.is_collected = !img.is_collected
        // 关闭 loading
        img.loading = false
      })
    },
    onDelete (img) {
      deleteImage(img.id).then(res => {
        // console.log(res)

        img.loading = true
        // 重新加载数据列表
        this.loadImages(this.page)
        img.loading = false
      })
    }
  }
}
</script>

<style scoped lang="less">
.action-head {
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
}
.image-item {
  position: relative;
}
.image-action {
  height: 40px;
  background-color: rgba(204, 204, 204, .5);
  position: absolute;
  bottom: 4px;
  left: 5px;
  right: 5px;
  font-size: 25px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
}
.selected {
  background: url(./selected.png) no-repeat;
  background-size: cover;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
