<template>
  <!-- <div class="layout-container">
    <div>顶部导航栏</div>
    <div>侧边导航栏</div> -->
    <!-- 子路由出口 -->
    <!-- <router-view />
  </div> -->
  <el-container class="layout-container">
    <el-aside class="aside" width="auto">
      <!-- <AppAside /> -->
      <app-aside class="aside-menu" :is-collapse="isCollapse" />
    </el-aside>
    <el-container>
      <el-header class="header">
        <div class="header-left">
          <i
            :class="{'el-icon-s-fold': !isCollapse,'el-icon-s-unfold': isCollapse}"
            @click="isCollapse = !isCollapse"
            style="font-size: 25px"
          ></i>
          <span>每天资讯—美好生活，天天不重样！</span>
        </div>
        <el-dropdown>
          <!-- <span class="el-dropdown-link">
            下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
          </span> -->
          <div class="avater-wrap">
            <img class="avater" :src="user.photo" alt="">
            <span>{{ user.name }}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>设置</el-dropdown-item>
            <!-- 组件默认是不识别原生事件的，除非内部做了处理 -->
            <!-- .native 是原生事件修饰符 -->
            <el-dropdown-item @click.native="onLogout">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-main class="main">
        <!-- 子路由出口 -->
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import AppAside from './components/aside'
import { getUserProfile } from '@/api/user'
import globalBus from '@/utils/global-bus'

export default {
  name: 'LayoutIndex',
  props: {},
  components: {
    AppAside
  },
  data () {
    return {
      user: {}, // 当前登录用户信息
      isCollapse: false // 侧边菜单栏的展示状态
    }
  },
  computed: {},
  watch: {},
  created () {
    // 组件初始化好，请求获取用户资料
    this.loadUserProfile()

    // 注册自定义事件
    // 当这个事件发生以后，这个注册函数就会被调用到
    globalBus.$on('update-user', data => {
      console.log('update-user', data)
      // this.user = data // 注意：不要这么做，对象之间赋值的引用，会导致相互影响的问题
      this.user.name = data.name
      this.user.photo = data.photo
    })
  },
  mounted () {},
  methods: {
    // 除了登录接口，其它所有接口都需要授权才能请求使用
    // 或者说，除了登录接口，其它接口都需要提供你的身份令牌才能获取数据
    loadUserProfile () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data.data
      })
    },
    onLogout (done) {
      // console.log('onLogout')

      this.$confirm('确认退出吗?', '退出提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 退出就是把用户登录状态清除
        window.localStorage.removeItem('user')
        // 然后跳转到登录页面
        this.$router.push('/login')
        this.$message({
          type: 'success',
          message: '退出成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消退出！'
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.layout-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.aside {
  background-color: #34495e;
  .aside-menu {
    height: 100%;
  }
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
  .header-left{
    display: flex;
    align-items: center;
  }
}
.avater-wrap {
  display: flex;
  align-items: center;
  .avater {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }
}
.main {
  background-color: #e9eef3;
}
</style>
