<template>
  <div class="settings-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!-- 面包屑路径导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>个人设置</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- /面包屑路径导航 -->
      </div>
      <el-row>
        <el-col :span="12">
          <el-form ref="setting-form" :model="user" label-width="95px" :rules="formRules">
            <el-form-item label="编号：">
              {{ user.id }}
            </el-form-item>
            <el-form-item label="手机：">
              {{ user.mobile }}
            </el-form-item>
            <el-form-item label="媒体名称：" prop="name">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item label="媒体简介：" prop="intro">
              <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item label="邮箱：" prop="email">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onUpdateUser" :loading="updateProfileLoading">保存设置</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :offset="4" :span="6">
          <!-- <p @click="$refs.file.click()">点击修改头像</p>
          <input type="file" hidden ref="file"> -->
          <!-- <label for="file">点击修改头像</label>
          <input id="file" type="file" hidden> -->
          <label for="file">
            <el-avatar
              shape="square"
              :size="150"
              fit="cover"
              src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"
            ></el-avatar><!-- :src="user.photo" -->
            <p class="text-file">点击修改头像</p>
          </label>
          <input id="file" type="file" hidden @change="onFileChange" ref="file" multiple><!-- multiple 可以设置多选 -->
        </el-col>
      </el-row>
    </el-card>
    <el-dialog
      title="修改头像"
      :visible.sync="dialogVisible"
      append-to-body
      @opened="onDialogOpened"
      @closed="onDialogClosed"
    >
      <!-- 图片裁切器 -->
      <div class="preview-image-wrap">
        <img
          class="preview-image"
          :src="previewImage"
          ref="preview-image"
        >
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onUpdatePhoto" :loading="updatePhotoLoading">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getUserProfile, updateUserPhoto, updateUserProfile } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'

// 获取图片 DOM 节点
// const image = document.getElementById('image')

// const image = this.$refs['preview-image']
// 初始化裁切器
// const cropper = new Cropper(image, {
//   aspectRatio: 16 / 9,
//   crop(event) {
//     console.log(event.detail.x)
//     console.log(event.detail.y)
//     console.log(event.detail.width)
//     console.log(event.detail.height)
//     console.log(event.detail.rotate)
//     console.log(event.detail.scaleX)
//     console.log(event.detail.scaleY)
//   }
// })

export default {
  name: 'SettingsIndex',
  components: {},
  props: {},
  data () {
    return {
      user: { // 用户资料
        id: null,
        mobile: '',
        name: '',
        intro: '',
        email: '',
        photo: ''
      },
      dialogVisible: false, // 控制上传图片裁切预览的显示状态
      previewImage: '', // 预览图片
      cropper: null, // 裁切器实例
      updatePhotoLoading: false, // 更新用户头像 loading 状态
      updateProfileLoading: false, // 更新基本信息的 loading 状态
      formRules: {
        name: [
          { required: true, message: '请输入媒体名称', trigger: 'blur' },
          { min: 5, max: 30, message: '长度在 5 到 30 个字符', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '请输入文章内容', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUser()
  },
  mounted () {},
  methods: {
    onUpdateUser () {
      // 表单验证
      // console.log(this.$refs['setting-form'])
      this.$refs['setting-form'].validate(valid => {
        // console.log(valid)
        if (!valid) {
          // 验证失败，停止提交
          return
        }
        // 开启 loading
        this.updateProfileLoading = true
        // 验证通过，提交表单
        const { name, intro, email } = this.user
        updateUserProfile({
          name,
          intro,
          email
        }).then(res => {
          // 关闭 loading
          this.updateProfileLoading = false
          // console.log(res)
          this.$message({
            type: 'success',
            message: '保存成功'
          })

          // 发布通知，用户信息已修改
          globalBus.$emit('update-user', this.user)
        })
      })
    },
    loadUser () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data.data
      })
    },
    onFileChange () {
      // 处理图片预览
      const file = this.$refs.file
      // console.log(file.files)
      // console.log(file.files[0])
      const blobData = window.URL.createObjectURL(file.files[0])
      // console.log(blobData)
      this.previewImage = blobData

      // 展示弹出层，浏览用户选择的图片
      this.dialogVisible = true

      // 图片裁切器必须基于 img 进行初始化
      // 注意：img 必须是可见状态才能正常完成初始化
      // const image = this.$refs['preview-image']
      // const cropper = new Cropper(image, {
      //   aspectRatio: 16 / 9,
      //   crop (event) {
      //     console.log(event.detail.x)
      //     console.log(event.detail.y)
      //     console.log(event.detail.width)
      //     console.log(event.detail.height)
      //     console.log(event.detail.rotate)
      //     console.log(event.detail.scaleX)
      //     console.log(event.detail.scaleY)
      //   }
      // })

      // console.log('file change') // 同一文件不会触发

      // 解决选择相同文件不触发 change 事件的问题
      // 相当于每次选择之后都会清空
      this.$refs.file.value = ''
    },
    onDialogOpened () {
      const image = this.$refs['preview-image']
      // 第一次初始化好以后，如果预览的裁切图片发送了变化，裁切器默认不会更新
      // 如果需要预览图片发生变化更新裁切器：
      //        方式一：裁切器 .replace 方法
      //        方式二：销毁裁切器，重新初始化

      // 方式一
      // if (this.cropper) {
      //   this.cropper.replace(this.previewImage)
      //   return
      // }

      // 不管是方式一还是方式二都要加上这段
      this.cropper = new Cropper(image, {
        // aspectRatio: 16 / 9
        aspectRatio: 1, // 这是裁切的比例:2/1、4/1、1/1(1)
        viewMode: 1 // 限制裁剪框不超过画布的大小

        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }
      })
    },
    onDialogClosed () {
      // 方式二
      // 对话框关闭，销毁裁切器
      this.cropper.destroy()
    },
    onUpdatePhoto () {
      // 让确定按钮开始 loading
      this.updatePhotoLoading = true

      // 1.获取裁切的图片对象
      this.cropper.getCroppedCanvas().toBlob(file => {
        // console.log(file)
        const fd = new FormData()
        fd.append('photo', file)
        // 2.请求更新用户头像,请求提交 fd
        updateUserPhoto(fd).then(res => {
          // console.log(res)
          // 3.关闭对话框
          this.dialogVisible = false
          // 4.更新视图的展示
          // 把服务端返回的图片进行展示优点慢
          // this.user.photo = res.data.data.photo

          // 这样更快，直接把裁切结果的文件对象转为 blob 数据，本地预览
          this.user.photo = window.URL.createObjectURL(file)

          // 关闭确定按钮的 loading
          this.updatePhotoLoading = false

          this.$message({
            type: 'success',
            message: '修改成功'
          })

          // 更新头部当前登录用户的信息
          // 发布通知，用户信息已修改
          globalBus.$emit('update-user', this.user)
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.text-file {
  margin: 16px 25px;
}
.preview-image-wrap{
  .preview-image {
    display: block;
    max-width: 100%;
    // height: 200px;
  }
}

</style>
