<template>
  <div class="login-container">
    <div class="login-form-wrap">
      <div class="login-head">
        <div class="logo"></div>
      </div>
      <!--
        配置 Form 表单验证：
          1.必须给 el-from 组件绑定 model 为表单数据对象
          2.给需要验证的表单项 el-form-item 绑定 prop 属性
            注意：prop 属性需要指定表单对象中的数据名称
          3.通过 el-form 组件的 rules 属性配置验证规则

          手动触发表单验证：
            1.给 el-form 设置 ref 起个名字
            2.通过 ref 获取 el-form 组件，调用组件的 validate 方法进行验证
       -->
      <!-- el-form 表单组件的每个表单项都必须使用 el-form-item 组件包裹住 -->
      <el-form
        class="login-form"
        ref="login-form"
        :model="user"
        :rules="formRules"
      >
        <el-form-item prop="mobile">
          <el-input v-model="user.mobile" placeholder="请输入手机号"></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model="user.code" placeholder="请输入验证码"></el-input>
        </el-form-item>
        <el-form-item prop="agree">
          <!-- <el-checkbox v-model="checked">我已阅读并同意用户协议和隐私条款</el-checkbox> -->
          <el-checkbox v-model="user.agree">我已阅读并同意用户协议和隐私条款</el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button
            class="login-btn"
            type="primary"
            @click="onLogin"
            :loading="loginLoading"
          >登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// import request from '@/utils/request'
// import { login, getUserInfo, updateUser } from '@/api/user'
import { login } from '@/api/user'

export default {
  name: 'LoginIndex',
  Components: {},
  props: {},
  data () {
    return {
      user: {
        mobile: '', // 手机号
        code: '', // 验证码
        agree: false // 是否同意协议
      },
      // checked: false, // 是否同意协议的选中状态
      loginLoading: false, // 登录的 loading 状态
      formRules: { // 表单验证规则配置
        // 要验证的数据名称: [规则列表]
        mobile: [
          // required: true 就是要求该字段是必填项
          // message 是提示消息 ，trigger 是触发验证的机制，blur 是当失去焦点的时候触发
          { required: true, message: '请输入手机号！', trigger: 'blur' },
          // 该字段长度的限制最小为3，最大为5，当失去焦点的时候触发验证的机制
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          { pattern: /^1[3|5|7|8|9]\d{9}$/, message: '请输入正确的手机号！', trigger: 'change' }
        ],
        code: [
          { required: true, message: '验证码不能为空！', trigger: 'blur' },
          // 默认验证规则是 trigger: 'change'，change 是在输入时触发
          { pattern: /^\d{6}$/, message: '请输入正确格式的验证码！' }
        ],
        agree: [
          {
            // 自定义验证规则
            //   验证通过：callback
            //   验证失败：callback(new Error('错误消息'))
            validator: (rule, value, callback) => {
              // console.log(rule, value)
              if (value) {
                callback()
              } else {
                callback(new Error('请勾选用户协议！'))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    onLogin () {
      // console.log('submit!')
      // 1.获取表单数据(根据接口要求绑定数据)
      // const user = this.user

      // 2.表单验证

      // 可以查看输出的方法
      // console.log(this.$refs['login-form'])

      // this.$refs['login-form'].validate((valid, error) => {
      //   console.log(valid) // 可通过 valid 知道表单验证是否通过
      //   console.log(error) // error 是验证失败的字段
      // })

      // validate 方法是异步的
      this.$refs['login-form'].validate(valid => {
        // 如果表单验证失败，停止请求提交
        if (!valid) {
          return
        }
        // 验证通过，提交请求登录
        this.login()
      })
    },
    login () {
      // 开启登录中 loading...
      this.loginLoading = true

      // 对于代码中的请求操作
      //    1.接口请求可能需要重用
      //    2.实际工作中，接口非常容易变动，改起来麻烦
      // 基于以上原因，建议的做法是把所有的请求都封装成函数，然后统一的组织到模块中进行管理
      // 这样做的好处就是：管理和维护更方便，也好重用
      // 所以进行以下优化
      // request({
      //   method: 'POST',
      //   url: '/mp/v1_0/authorizations',
      //   // data 用来设置 POST 请求体
      //   data: this.user
      // }).then(res => {
      // 这里需要加载 user.js
      login(this.user).then(res => {
        // 登录成功
        // console.log(res)

        this.$message({
          message: '登录成功',
          type: 'success'
        })

        // 将接口返回的用户相关数据放到本地存储，方便应用数据共享
        // 本地存储只能存储字符串
        // 如果需要存储对象、数组类型的数据，则把他们转为 JSON 格式字符串进行存储
        window.localStorage.setItem('user', JSON.stringify(res.data.data))

        // 关闭 loading
        this.loginLoading = false
      }).catch(err => {
        // 登录失败
        console.log('登录失败', err)

        this.$message.error('登录失败，手机号或验证码错误！')

        // 关闭 loading
        this.loginLoading = false

        // 跳转到首页
        // this.$router.push('/')
        // 下面这种写法也可以，更具语义性
        this.$router.push({
          name: 'home'
        })
      })
      // 3.处理后端响应结果
      //    成功：xxx
      //    失败：xxx
    }
  }
}
</script>

<style scoped lang="less">
.login-container {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("./login_bg.jpg") no-repeat;
  // background-size: contain;  // 以长边的方式显示，等比例模式
  background-size: cover;  // 以短边的方式显示，背景填充模式
  .login-form-wrap{
    background-color: #fff;
    .login-head {
      padding: 15px 0;
      height: 52px;
      .logo {
        margin: auto;
        width: 167px;
        height: 100%;
        background: url("./logo_index.png") no-repeat;
      }
    }
    .login-form {
      padding: 0 50px;
      min-width: 300px;
      .login-btn {
        width: 100%;
      }
    }
  }
}
</style>
