<template>
  <div class="fans-container">
      <!-- 1.为 ECharts 准备一个具备大小的画布容器 DOM -->
      <div ref="main" style="width: 600px;height:400px"></div>
      <h2>地图图表示例</h2>
      <map-demo />
  </div>
</template>

<script>
// 2.加载 echarts
import * as echarts from 'echarts'
import MapDemo from './commponents/map-demo.vue'
// 如果使用依赖百度地图的图表，则必须
//    1.加载百度地图的 JavaScript SDK -> pubilc/index.html
//    2.加载 echarts 百度地图的扩展 -> import 'echarts/dist/extension/bmap'

export default {
  name: 'FansIndex',
  components: {
    MapDemo
  },
  props: {},
  data () {
    return {}
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {
    // 注意：初始化操作 DOM 一定要写到 mounted 钩子函数中
    // 3.基于准备好的 dom ，初始化 echarts 实例
    const myChart = echarts.init(this.$refs.main)

    // 指定图表的配置项和数据
    const option = {
      title: {
        text: 'ECharts 入门示例'
      },
      tooltip: {},
      legend: {
        data: ['销量']
      },
      xAxis: {
        type: 'category',
        data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
      },
      yAxis: {},
      series: [
        {
          name: '销量',
          data: [120, 200, 150, 80, 70, 110],
          type: 'bar',
          showBackground: true,
          backgroundStyle: {
            color: 'rgba(180, 180, 180, 0.2)'
          }
        }
      ]
    }
    // 使用刚指定的配置项和数据显示图表
    myChart.setOption(option)
  },
  methods: {}
}
</script>

<style scoped lang="less">

</style>
